@mixin field-help-text {
  $text-color--muted:         hsl(0, 0%, 50%) !default;
  $font-size--small:          0.75em !default;

  display: block;
  padding-top: 0.4em;
  font-size: $font-size--small;
  line-height: 1.2em;
  color: $text-color--muted;

  &:empty {
    display: none;
  }
}


@mixin field-focus {
  $color-main:                hsl(208, 98%, 43%) !default;
  $opacity:                   0.7 !default;

  outline: 0;
  // box-shadow: 0 0 0 2px rgba(#427eec, 0.5);
}


@mixin field-name {
  $line-height:                 1.375em !default;

  display: block;
  margin-bottom: 0.4em;
  line-height: $line-height;
  font-weight: 400;
  font-size: 14px;
  color: #a3a6ab;
}
