@font-face {
  font-family: 'ALS Rubl';
  src: local('ALS Rubl'),
      url('../fonts/rouble.woff') format('woff');
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: 'SF UI Display';
  src: url('../font/SFUIDisplay-Regular.eot');
  src: local('SF UI Display Regular'), local('SFUIDisplay-Regular'),
      url('../fonts/SFUIDisplay-Regular.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Regular.woff2') format('woff2'),
      url('../fonts/SFUIDisplay-Regular.woff') format('woff'),
      url('../fonts/SFUIDisplay-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SF UI Display';
  src: url('../fonts/SFUIDisplay-Bold.eot');
  src: local('SF UI Display Bold'), local('SFUIDisplay-Bold'),
      url('../fonts/SFUIDisplay-Bold.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Bold.woff2') format('woff2'),
      url('../fonts/SFUIDisplay-Bold.woff') format('woff'),
      url('../fonts/SFUIDisplay-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'SF UI Display';
  src: url('../fonts/SFUIDisplay-Black.eot');
  src: local('SF UI Display Black'), local('SFUIDisplay-Black'),
      url('../fonts/SFUIDisplay-Black.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Black.woff2') format('woff2'),
      url('../fonts/SFUIDisplay-Black.woff') format('woff'),
      url('../fonts/SFUIDisplay-Black.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
}


body {
  // ТОЛЬКО типографика!
  font-family: $font-family;
  font-size: $font-size;
  line-height: $line-height;
  color: $text-color;

  @media (max-width: $screen-sm) {
    font-size: 14px;
  }
}

// .h1,
// .h2,
// .h3,
// .h4,
// .h5,
// .h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family--headings;
  font-weight: 700;
  line-height: $line-height;
  color: currentColor;
}

// .h1,
// .h2,
// .h3,
h1,
h2,
h3 {
  margin-top: $line-height;
  margin-bottom: ($line-height / 2);
}

// .h4,
// .h5,
// .h6,
h4,
h5,
h6 {
  margin-top: ($line-height / 2);
  margin-bottom: ($line-height / 2);
}

// .h1,
h1 {
  font-size: 24px;

  @media (min-width: $screen-md) {
    font-size: ($font-size--h1 / 2);
  }

  @media (min-width: $screen-lg) {
    font-size: $font-size--h1;
  }
}

// .h2,
h2 {
  font-size: 20px;

  @media (min-width: $screen-md) {
    font-size: ($font-size--h2 / 1.5);
  }

  @media (min-width: $screen-lg) {
    font-size: $font-size--h2;
  }
}

// .h3,
h3 {

  font-size: 18px;

  @media (min-width: $screen-md) {
    font-size: ($font-size--h3 / 1.3);
  }

  @media (min-width: $screen-lg) {
    font-size: $font-size--h3;
  }
}

// .h4,
h4 {
  font-size: 16px;

  @media (min-width: $screen-md) {
    font-size: ($font-size--h4 / 1.2);
  }

  @media (min-width: $screen-lg) {
    font-size: $font-size--h4;
  }
}

// .h5,
h5 {
  font-size: $font-size--h5;
}

// .h6,
h6 {
  font-size: $font-size--h6;
}

// .p,
p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
  margin-top: ($line-height / 2);
  margin-bottom: ($line-height / 2);
}

address {
  font-style: normal;
}

ul,
ol {
  padding-left: 1.2em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;

  @media (min-width: $screen-sm) {
    margin-left: 1.8em;
  }

  & + dt {
    margin-top: ($line-height / 2);
  }
}

hr {
  margin-top: $line-height;
  margin-bottom: $line-height;
  border: 0;
  border-top: 1px solid $border-color;
}

// .small,
small {
  font-size: $font-size--small;
}

sub,
sup {
  font-size: 70%;
}

// .mark,
mark {
  background: #fcf8e3;
  padding: 0.1em 0.3em;
}

b,
strong {
  font-weight: bolder;
}

abbr[title] {
  text-decoration: underline dotted;
}

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}

code,
kbd,
pre,
samp {
  font-family: $font-family--monospace;
}

code {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;
}

kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;

  kbd {
    padding: 0;
  }
}

pre {
  display: block;
  width: 100%;
  overflow-x: auto;
  tab-size: 2;
}

.rub {
  font-family: 'ALS Rubl';
}

.icon {
  fill:#7eaafa;

  &--success {
    fill: #84c538;
  }
}

.garant-info {
  margin-top: 1.2em;
  font-size: 14px;
  .icon {
    margin-right: 10px;
  }

  @media (min-width: $screen-md) {
    font-size: 16px;
  }

  @media (max-width: $screen-sm) {
    font-size: 12px;

    .icon {
      margin-right: 5px;
      width: 16px;
      height: 16px;
    }
  }
}
