// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

$gray-lighter:                hsl(0, 0%, 80%) !default;
$gray-lightest:               hsl(0, 0%, 90%) !default;
$text-color:                  hsl(0, 0%, 10%) !default;
$line-height:                 1.375em;

$field-padding-vertical:      0.3em !default;
$field-padding-horizontal:    0.7em !default;

$border-radius:               3px !default;

.btn {

  $block-name:                &; // #{$block-name}__element

  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  padding: 14px 48px;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  text-align: center;
  font-size: 18px;
  font-family: inherit;
  line-height: $line-height;
  text-decoration: none;
  text-transform: none;
  color: $text-color;
  border-radius: $border-radius;
  border: 1px solid $gray-lighter;
  background-color: $gray-lightest;
  background-image: none;
  cursor: pointer;
  transition: all $transition-time;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $text-color;
    background-color: darken($gray-lightest, 10%);
    border-color: darken($gray-lighter, 12%);
  }

  &:active,
  &:focus {
    @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }

  @media (max-width: $screen-sm) {
    padding: 14px 24px;
    font-size: 16px;
    width: 100%;
  }

  &__icon {
    display: inline-block;
    vertical-align: middle;
  }

  &--outline-green {
    color: #353638;
    background-color: transparent;
    border: 2px solid #7cbb32;

    &:hover,
    &:focus {
      color: #fff;
      background-color: lighten(#7cbb32, 8%);
      border-color: lighten(#7cbb32, 10%);
    }
  }


  &--main {
    color: #fff;
    background-color: $color-main;
    border: 1px solid $color-main;

    &:hover,
    &:focus {
      color: #fff;
      background-color: darken($color-main, 10%);
      border-color: darken($color-main, 12%);
    }

    &--disabled,
    &:disabled {
      color: #fff;
      background-color: #6499fa;
      border-color: #6499fa;
    }

    #{$block-name}__icon {
      fill: #fff;
      display: inline-block;

      @media (min-width: $screen-lg) {
        display: none;
      }
    }
  }
}
